
import { defineComponent } from 'vue'
import logo from '@/assets/images/logo.svg'

export default defineComponent({
  name: 'Header',
  props: {
    menu: Boolean
  },
  data () {
    return {
      logo
    }
  },
  methods: {
    onClickBars () {
      this.$emit('show-menu')
    }
  }
})
