
import { defineComponent, watchEffect, ref } from 'vue'
import logo from '@/assets/images/loading.png'

export default defineComponent({
  name: 'MaskLoading',
  props: {
    active: Boolean
  },
  setup (props) {
    const img = logo

    watchEffect(() => {
      document.body.style.overflow = props.active
        ? 'hidden'
        : 'initial'
    })

    return { img }
  }
})
